<template>
  <!-- 攻略 -->
  <div class="strategy">
    <div class="top">
      <!-- <img src="~@/assets/home/strategy_top@2x.png" alt="" /> -->
    </div>
    <div class="content">
      <div class="bgwrap bgt"></div>
      <div class="box">
        <div ref="rule" class="rule rule-lh tal">{{ rule }}</div>
      </div>
      <div class="bgwrap bgb"></div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Shop',
  data () {
    return {
      rule: ''
    }
  },
  components: {
  },
  created () {
    this.getRule()
  },
  methods: {
    // 获取攻略
    async getRule () {
      let res = await this.$request('get', 'activity/getrule', { type: 1 })
      // console.log('noticeList', res)
      if (res && res.error_no === '0') {
        // this.noticeList = res.data
        console.log(res.data)
        this.rule = res.data
        this.$refs.rule.innerHTML(res.data)
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.strategy {
  // min-height: calc(100vh - 8vw);
  background-image: url(~@/assets/home/strategy_bg@2x.png);
  background-size: 100% 100%;
  min-height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  // .bg-image("../assets/home/strategy_bg","png");
  padding-top: 320px;
  .top {
    width: 100%;
    height: 274px;
    // height: 280px;
    // height: 459px;
    background-image: url(~@/assets/home/strategy_top@2x.png);
    // background-image: url(~@/assets/home/sss2@2x.png);
    background-size: 110% 274px;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    // > img {
    //   width: 622px;
    //   height: 274px;
    // }
  }
  .content {
    width: 100%;
    height: 710px;
    padding: 0 32px 8px;
    border-color: #feeebc;
    .flex_func(column, center,center,nowrap);
    .bgwrap {
      height: 40px;
      width: 646px;
      background: #fff;
      border-radius: 20px;
      border: 2px solid #feeebc;
    }
    .bgt {
      margin-bottom: -20px;
    }
    .bgb {
      margin-top: -20px;
    }
    .box {
      width: 686px;
      height: calc(100% - 30px);
      z-index: 2;
      padding: 32px 28px 32px 32px;
      // overflow-y: auto;
      background: #fff;
      border-radius: 16px;
      font-size: 24px;
      color: #333;
      line-height: 34px;
      .rule {
        line-height: 34px;
        color: #333;
        width: 100%;
        height: calc(100% - 5px);
        overflow-y: auto;
      }
      p {
        line-height: 34px;
      }
      .pcenter {
        margin: 30px 0;
      }
    }
  }
}
</style>



